<template>
    <div class="body">
        <div class="header">
            <van-nav-bar fixed placeholder z-index="999" title="标题" left-text="返回" @click-left="goBack" left-arrow>
                <van-icon name="search" slot="right" />
            </van-nav-bar>
        </div>
        <div class="main">
            <br>
            <van-form @submit="onSubmit">
            <van-cell-group>
                <van-uploader name="uploader" :after-read="onRead1">
                    <van-icon name="photograph" />
                </van-uploader>
                <br>
                <van-uploader name="uploader" :after-read="onRead2">
                    <van-icon name="photograph" />
                </van-uploader>
            <van-field
                v-model="title"
                required
                clearable
                label="标题"
                right-icon="question-o"
                placeholder="请输入标题"
                @click-right-icon="$toast('活动标题')"
            />

            <van-field
                v-model="editor"
                type="text"
                label="编辑"
                placeholder="请输入编辑人"
                required
            />
            <van-field
                v-model="tag1"
                type="text"
                label="标签1"
                placeholder="请输入标签1"
                required
            />
            <van-field
                v-model="tag2"
                type="text"
                label="标签2"
                placeholder="请输入标签2"
                required
            />
            <van-field
                v-model="tag3"
                type="text"
                label="标签3"
                placeholder="请输入标签3"
                required
            />
            <van-field
                v-model="p1"
                type="textarea"
                label="段落1"
                placeholder="请输入段落1"
                required
            />
            <van-field
                v-model="p2"
                type="textarea"
                label="段落2"
                placeholder="请输入段落2"
                required
            />
            <van-field
                v-model="p3"
                type="textarea"
                label="段落3"
                placeholder="请输入段落3"
                required
            />
            <van-field
                v-model="p4"
                type="textarea"
                label="段落4"
                placeholder="请输入段落4"
                required
            />
            <van-field
                v-model="p5"
                type="textarea"
                label="段落5"
                placeholder="请输入段落5"
                required
            />
            <div style="margin: 16px;">
                                <van-button round block type="info" native-type="submit">提交</van-button>
                            </div>
            </van-cell-group>
            </van-form>
        </div>
        <div class="footer"></div>
    </div>
</template>
<style>
.body{
    min-height: 100vh;
    background-color: #f1f5f7;
}

.header{

}

.main{

}

.footer{

}
</style>
<script>
import axios from 'axios';
import { Toast } from 'vant';

    const options = {
        data(){
            return {
                title: null,
                editor: null,
                tag1: "",
                tag2: "",
                tag3: "",
                p1: "",
                p2: "",
                p3: "",
                p4: "",
                p5: "",
                img1: "",
                img2: "",
            }
        },
        computed:{

        },
        methods:{
            goBack(){
                this.$router.push("/manage");
            },
            onRead1(file){
                this.img1 = file;
            },
            onRead2(file){
                this.img2 = file;
            },
            onSubmit(){
                let form = new FormData();
                form.set("img1",this.img1.file);
                form.set("img2",this.img2.file);
                form.set("title",this.title);
                form.set("editor",this.editor);
                form.set("tag1",this.tag1);
                form.set("tag2",this.tag2);
                form.set("tag3",this.tag3);
                form.set("p1",this.p1);
                form.set("p2",this.p2);
                form.set("p3",this.p3);
                form.set("p4",this.p4);
                form.set("p5",this.p5);
                axios.post("/admin/upactivity",form).then((resp)=>{
                    Toast(resp.data.msg);
                });
            }
        },
        mounted(){

        }
    };

    export default options;
</script>